const KEY_CONSUMPTION_ID_GENERATOR = "KEY_CONSUMPTION_ID_GENERATOR";
const KEY_ALIMENTS_ID_GENERATOR = "KEY_ALIMENTS_ID_GENERATOR";

class IdGenerator {

    generateAlimentId() {
        return this.generateId(KEY_ALIMENTS_ID_GENERATOR);
    }

    generateConsumptionId() {
        return this.generateId(KEY_CONSUMPTION_ID_GENERATOR);
    }

    generateId(key) {
        this.initGenerator(key);
        let newId = parseInt(localStorage.getItem(key), 10) + 1;
        localStorage.setItem(key, newId);
        return newId;
    }

    initGenerator(key) {
        if (!localStorage.getItem(key)) {
            localStorage.setItem(key, 0);
        }
    }
}

export default IdGenerator;


