import React from 'react';
import AlimentTabItem from './AlimentTabItem';

function AlimentList(props) {
  return (
    <div>
      <table className="table table-striped">
        <thead>
          <tr>
            <th scope="col">Label</th>
            <th scope="col">kcal/100g</th>
            <th scope="col">kcal/1</th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {
            props.aliments.map(aliment =>
              <AlimentTabItem key={aliment.id} aliment={aliment} onConsume={props.onConsume} />)
          }
        </tbody>
      </table>
    </div>
  );
}

export default AlimentList;



