import './style.css';
import React from 'react';

function MenuNavLinks(props) {
  return (
    <div className="col-3">
      <div className="nav flex-column nav-pills" id="v-pills-tab" role="tablist" aria-orientation="vertical">
        {props.children}
      </div>
    </div>
  );
}
export default MenuNavLinks;



