import './style.css';
import React, { Component } from 'react';

class MenuNavLink extends Component {

  constructor(props) {
    super(props);
    this.state = {
      active: this.props.active,
    };
  }

  render() {
    return (
      <a className={this.props.active ? "nav-link active" : "nav-link"}
        id={this.props.linkId + "-tab"}
        data-toggle="pill"
        href={"#" + this.props.linkId}
        role="tab"
        aria-controls={this.props.linkId}
        aria-selected={this.props.active}
        onClick={() => this.props.onClick(this.props.linkId)}
      >
        {this.props.children}
      </a>
    );
  }
}

export default MenuNavLink;



