import './style.css';
import React from 'react';
import CenterModal from "./CenterModal";
import ConsumptionForm from './ConsumptionForm';

function AlimentTabItem(props) {
  //const formId = "consumption-selector-form-" + props.aliment.id;

  return (
    <tr key={props.aliment.id} >
      <td>{props.aliment.label}</td>
      <td>{props.aliment.energyPer100g}</td>
      <td>{props.aliment.energyPerUnit}</td>
      <td>
        <CenterModal unique={props.aliment.id} buttonLabel="Eat me" buttonClassName="btn btn-outline-success btn-sm">
          <ConsumptionForm selectedAliment={props.aliment} onSave={props.onConsume} />
        </CenterModal>
      </td>
    </tr>
  );
}
export default AlimentTabItem;



