import './style.css';
import React, { Component } from 'react';
import AlimentModel from '../../models/AlimentModel';
import IdGenerator from '../../IdGenerator';
import * as common from '../../util/common';

class AlimentEditor extends Component {

  constructor(props) {
    super(props);
    this.state = {
      hideForm: false
    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleSubmit(event) {
    event.preventDefault();
    let aliment = new AlimentModel(this.state.label);
    aliment.energyPer100g = this.state.energyPer100g;
    aliment.energyPerUnit = this.state.energyPerUnit;
    aliment.id = (new IdGenerator()).generateAlimentId();
    this.props.onSubmit(aliment);
  }

  handleChange(event) {
    const name = event.target.name;
    const inputValue = event.target.value;
    this.setState({
      [name]: name === 'label' ? common.firstLetterToUpperCase(inputValue) : inputValue
    });
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <h2>Edit aliment</h2>
        <div className="form-group">
          <label htmlFor="AliementEditorLabel">Label</label>
          <input type="text"
            className="form-control"
            id="AliementEditorLabel"
            placeholder="His name is ..."
            name="label"
            autoComplete="off"
            value={this.props.aliment.label}
            onChange={this.handleChange} required />
        </div>

        <div className="form-group">
          <label htmlFor="AliementEditorEnergyPer100g">Energy per 100g</label>
          <input type="text"
            className="form-control"
            id="AliementEditorEnergyPer100g"
            placeholder="Calories per 100g"
            name="energyPer100g"
            autoComplete="off"
            pattern="\d*"
            value={this.props.aliment.energyPer100g}
            onChange={this.handleChange} required />
        </div>

        <div className="form-group">
          <label htmlFor="AliementEditorEnergyPerUnit">Energy per unit</label>
          <input type="text"
            className="form-control"
            id="AliementEditorEnergyPerUnit"
            placeholder="Calories per unit"
            name="energyPerUnit"
            autoComplete="off"
            pattern="\d*"
            value={this.props.aliment.energyPerUnit}
            onChange={this.handleChange} required />
        </div>

        <div className="form-group">
          <button type="submit" className="btn btn-primary">Save it</button> &nbsp;
          <button form="" onClick={this.props.hideForm} className="btn btn-light">Hide it</button>
        </div>

      </form>
    );
  }
}

export default AlimentEditor;



