
function dateToFrenchFormat(datetime) {
    return datetime.toLocaleString('fr-FR', { timeZone: 'Europe/Paris' });
}

export function dateStringIsoToFrench(stringDate) {
    return dateToFrenchFormat(new Date(Date.parse(stringDate)));
}

/**
 * Return the ISO date
 */
export function toIsoDate(isoDatetime) {
    return isoDatetime.split('T')[0];
}

export function getCurrentIsoDate() {
    return toIsoDate(getCurrentLocaleIsoDatetime().toISOString());
}

export function getCurrentIsoDatetime() {
    const parts = getCurrentLocaleIsoDatetime().toISOString().split(':');
    return parts[0] + ":" + parts[1];
}

function getCurrentLocaleIsoDatetime() {
    const offset = (new Date()).getTimezoneOffset();
    return new Date(Date.now() - offset * 60 * 1000);
}


