import './style.css';
import React from 'react';

function CenterModal(props) {
    return (
        <div>
            <button type="button" className={props.buttonClassName} data-toggle="modal" data-target={"#modalCenter" + props.unique}>
                {props.buttonLabel}
            </button>

            <div className="modal fade" id={"modalCenter" + props.unique} tabIndex="-1" role="dialog"
                aria-labelledby={"modalCenterTitle" + props.unique} aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-body">
                            {props.children}
                        </div>
                    </div>
                </div>
            </div>
        </div>

    );
}
export default CenterModal;



