import './style.css';
import React from 'react';
import * as DateUtil from '../util/date';
import StorageProcessorFactory from '../StorageProcessorFactory';

function ConsumedTabItem(props) {
  let howMush = props.item.quantity;
  howMush += (props.item.quantityUnit === "grammes") ? " g" : "";
  const alimentSP = new StorageProcessorFactory.getAlimentSP();
  const aliment = alimentSP.findById(props.item.alimentId);
  const label = aliment ? aliment.label : "[" + props.item.id + "]";

  return (
    <tr key={props.item.id} >
      <td>{label}</td>
      <td>{howMush}</td>
      <td>...kcal</td>
      <td>{DateUtil.dateStringIsoToFrench(props.item.datetime)}</td>
    </tr>
  );
}
export default ConsumedTabItem;



