import './style.css';
import React, { Component } from 'react';
import AlimentEditor from './AlimentEditor/AlimentEditor';
import AlimentModel from '../models/AlimentModel';
import AlimentList from './AlimentList';
import StorageProcessorFactory from '../StorageProcessorFactory';
import MenuTabPane from './MenuTabPane/MenuTabPane';
import SearchBar from './SearchBar';

class AlimentManagerTabPane extends Component {

    constructor(props) {
        super(props);

        this.alimentSP = new StorageProcessorFactory.getAlimentSP();
        this.consumptionSP = new StorageProcessorFactory.getConsumptionSP();
        this.searchValueChange = this.searchValueChange.bind(this);
        this.state = {
            defaultFilter: "",
            aliment: new AlimentModel(),
            aliments: [],
            consumedAliments: this.consumptionSP.findAll(),
        }
    }

    /**
     * @param {Aliment} aliment 
     */
    addAlimentToList = aliment => {
        this.alimentSP.addToList(aliment);
        const aliments = this.alimentSP.findAll();
        this.setState({ aliments });
    }

    searchValueChange(event) {
        event.preventDefault();
        const searchLabel = event.target.value;
        const foundAliments = this.alimentSP.findByName(searchLabel);
        this.setState({ aliments: foundAliments });
    }

    renderSearchBarTitle() {
        return (<span>Aliments <small>({this.alimentSP.count()} found)</small></span>);
    }

    render() {
        return (
            <MenuTabPane tabId={this.props.tabId} active={this.props.active}>
                <AlimentEditor aliment={this.state.aliment} onSubmit={this.addAlimentToList} />
                <SearchBar inputType="text" title={this.renderSearchBarTitle()} onSearchValueChange={this.searchValueChange} placeholder="Find an aliment ..." />
                <AlimentList aliments={this.state.aliments} onConsume={this.props.onConsume} />
            </MenuTabPane>
        );
    }
}
export default AlimentManagerTabPane;



