import './style.css';
import React from 'react';
import ConsumedTabItem from './ConsumedTabItem';

function ConsumedList(props) {
  return (
    <div>
      <table className="table table-striped">
        <thead>
          <tr>
            <th scope="col">What</th>
            <th scope="col">How much</th>
            <th scope="col">Intake</th>
            <th scope="col">When</th>
          </tr>
        </thead>
        <tbody>
          {props.consumedAliments.map(item => <ConsumedTabItem key={item.id} item={item} />)}
        </tbody>
        <tfoot>
          <tr>
            <th scope="col"></th>
            <th scope="col">Sum</th>
            <th scope="col">0 kcal</th>
            <th scope="col"></th>
          </tr>
        </tfoot>
      </table>
    </div>
  );
}

export default ConsumedList;



