import './style.css';
import React from 'react';

function MenuTabPane(props) {
  return (
    <div className={props.active ? "tab-pane fade show active" : "tab-pane fade"}
      id={props.tabId}
      role="tabpanel"
      aria-labelledby={props.tabId + "-tab"}
    >
      {props.children}
    </div>
  );
}
export default MenuTabPane;



