import ConsumptionStorageProcessor from "./ConsumptionStorageProcessor";
import AlimentStorageProcessor from "./AlimentStorageProcessor";

const KEY_ALIMENTS = "KEY_ALIMENTS";
const KEY_CONSUMPTION = "KEY_CONSUMPTION";

class StorageProcessorFactory {

    static getAlimentSP() {
        return new AlimentStorageProcessor(KEY_ALIMENTS);
    }

    static getConsumptionSP() {
        return new ConsumptionStorageProcessor(KEY_CONSUMPTION);
    }
}

export default StorageProcessorFactory;


