import './style.css';
import React, { Component } from 'react';
import * as DateUtil from '../util/date';
import IdGenerator from '../IdGenerator';
import ConsumptionModel from '../models/ConsumptionModel';

class ConsumptionForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      hideForm: false,
      quantity: undefined,
      quantityUnit: "grammes",
      datetime: DateUtil.getCurrentIsoDatetime(),
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    const name = event.target.name;
    const inputValue = event.target.value;
    this.setState({
      [name]: inputValue
    });
  }

  handleSubmit(event) {
    event.preventDefault();
    document.getElementById('btn-dismiss').click();
    const id = (new IdGenerator()).generateConsumptionId();
    const alimentId = this.props.selectedAliment.id;
    const datetime = this.state.datetime;
    const consumbtion = new ConsumptionModel(id, alimentId, datetime);
    consumbtion.quantity = this.state.quantity;
    consumbtion.quantityUnit = this.state.quantityUnit;
    this.props.onSave(consumbtion);
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit} id={this.props.formId}>
        <div className="form-group">
          <p>You ate <strong>{this.props.selectedAliment.label}</strong> ...</p>
        </div>

        <div className="form-group">
          <label htmlFor="ConsumptionSelectorQty">How much ?</label>
          <input type="text"
            pattern="\d*"
            className="form-control"
            id="ConsumptionSelectorQty"
            placeholder="How muchd..."
            name="quantity"
            autoComplete="off"
            onChange={this.handleChange} required />
        </div>

        <div className="form-group">
          <select className="form-control" name="quantityUnit" onChange={this.handleChange} required>
            <option defaultValue value="grammes">Grammes</option>
            <option value="unit">Unit</option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="ConsumptionSelectorDatetime">When ?</label>
          <input type="datetime-local"
            className="form-control"
            id="ConsumptionSelectorDatetime"
            placeholder="When..."
            name="datetime"
            value={this.state.datetime}
            onChange={this.handleChange} required />
        </div>

        <div className="form-group">
          <button id="btn-dismiss" type="button" className="btn btn-light" data-dismiss="modal">Close</button> &nbsp;
          <button type="submit" className="btn btn-primary">Save</button>
        </div>

      </form>
    );
  }
}

export default ConsumptionForm;



