import './style.css';
import React, { Component } from 'react';
import ConsumedList from './ConsumedList';
import MenuTabPane from './MenuTabPane/MenuTabPane';
import Alert from './Alert';
import DateSearchBar from './DateSearchBar';
import * as DateUtil from '../util/date';

class ConsumptionTabPane extends Component {

  constructor(props) {
    super(props);
    this.state = {
      searchDate: DateUtil.getCurrentIsoDate(),
    };
    this.searchByDate = this.searchByDate.bind(this);
    this.searchValueChange = this.searchValueChange.bind(this);
    this.messages = [
      "You have digested 0 of 0 kcal.",
      "Now you can eat the equivalent of 0 kcal."
    ];
  }

  searchValueChange(event) {
    event.preventDefault();
    const searchDate = event.target.value;
    this.setState({ searchDate });
  }

  searchByDate(event) {
    event.preventDefault();
    this.props.doFilter(this.state.searchDate);
  }

  render() {
    return (
      <MenuTabPane tabId={this.props.tabId} active={this.props.active}>
        <DateSearchBar title="Consuption" onSearchValueChange={this.searchValueChange} placeholder="Datetime ..."
          onSearch={this.searchByDate} defaultValue={this.state.searchDate} />
        <ConsumedList consumedAliments={this.props.consumedAliments} />
        <Alert title="Summary : " messages={this.messages} type="info" />
      </MenuTabPane>
    );
  }
}

export default ConsumptionTabPane;



