import './style.css';
import React from 'react';

function MenuTabPanes(props) {
  return (
    <div className="col-9">
      <div className="tab-content" id="v-pills-tabContent">
        {props.children}
      </div>
    </div>
  );
}
export default MenuTabPanes;



